import React from 'react';
import { Container, Row, Col } from 'reactstrap';
import {
    Card, CardImg, CardText, CardBody,
    CardTitle, Button
} from 'reactstrap';
import portfolios from "../Portfolios";

export default class Portfolio1 extends React.Component {
    constructor(props) {
        super(props)
    }


    render() {
        return (
            <div>
                <Container className="portfoilo">
                    <h2  className="wow slideInLeft"  style={{ color: "black", fontWeight: "bolder" }}> <i className="fa fa-hand-o-right"> Our Portfolio: </i></h2>

                    <Row style={{ marginTop: "30px" }}>
                        {
                            portfolios.map((item, index) => {
                                return (
                                    <Col key={index} sm="3">
                                        <Card className="card1 wow fadeInUp">
                                            <CardImg top width="100%" height="250PX" src={item.img} />
                                            <CardBody>
                                                <CardTitle><strong>{item.title}</strong></CardTitle>
                                                <Button className="Button1" outline color="warning"  size="sm" onClick={()=>{this.props.history.push('/portfoliodetail/'+item.id)}}> Read More</Button>
                                                <CardText></CardText>
                                            </CardBody>
                                        </Card>
                                    </Col>
                                );
                            })

                        }
                        
                    </Row>
                </Container>

            </div>
        )
    }
}