import React from 'react';
import { Container, Row, Col } from 'reactstrap';
import { CardImg } from 'reactstrap';
import portfolios from "../Portfolios";

export default class Portfoliodetail extends React.Component {
    constructor(props) {
        super(props)
    }


    render() {
        let portoflio = portfolios.find(item => item.id == this.props.match.params.portfolioId);
        if (!portoflio) {
            return null;
        }
        const { img, title, descripition } = portoflio;
        return (
            <div>
                <Container className="portfoilomodal">

                    <Row style={{ marginTop: "30px" }} className="portfoliodetail">
                        <Col md="6">
                            <CardImg height="460PX" style={{ marginTop: "15px" }} src={img} />
                        </Col>
                        <Col md="6">
                            <h3 style={{ marginTop: "20px", fontWeight: "bolder" }} className="wow bounceIn" >{title}</h3>
                            <br />
                            <p style={{ fontWeight: "500" }}>
                                <i className="fa fa-arrow-circle-right"></i>&nbsp;
                             {descripition}</p>
                        </Col>
                    </Row>
                </Container>

            </div>
        )
    }
}